.Option {
    margin-bottom: 20px;
}

.OptionLabel {
    float: left;
    width: 35%;
    text-align: right;
    margin-right: 5%;
}

.OptionLabel label {
    font-size: 18px;
    text-transform: uppercase;
    position: relative;
    top: 2px;
}

.OptionField {
    float: left;
    width: 60%;
    text-align: left;
}

.OptionField select {
    background: white;
    outline: none;
    border: 1px solid #ccc;
    padding: 5px;
    border-radius: 3px;
    width: 150px;
}
