.NoticeWrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.Notice {
    background: white;
    border-radius: 5px;
    box-shadow: 0 0 80px #333;
    padding: 10px 20px;
    border-bottom: 2px solid #ddd;
    text-align: center;
}
